/* btn
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.btn {
  position: relative;
  display: inline-block;
  width: 220px;
  max-width: 100%;
  padding: 15px 10px;
  font-size: rem(14);
  font-weight: bold;
  color: $c_white;
  text-align: center;
  text-decoration: none;
  background-color: $c_key1;
  background-size: 200% auto;
  border-radius: 50px;

  &:hover {
    color: $c_white;
    background-color: $c_key3;
  }
}
