/* section
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global" as *;

.section {
  padding: 50px 0;

  @include mq(md) {
    padding: 80px 0;
  }
}

.section__inner {
  max-width: $width;
  padding: $padding-side-sp;
  margin: 0 auto;

  @include mq(md) {
    padding: $padding-side;
  }
}

.section__title {
  max-width: $width_md;
  margin: 0 auto;
  margin-bottom: 40px;
  text-align: center;

  @include mq(md) {
    margin-bottom: 70px;
  }
}
