/* footer
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global"as *;

.footer {
  background-color: $c_key1;
}

.footer__inner {
  padding: 30px $padding-side-sp;
  text-align: center;

  @include mq(md) {
    padding: 60px $padding-side;
  }
}

.footer-nav {
  padding-bottom: 30px;
}

.footer-nav__list {
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.footer-nav__link {
  display: block;
  margin: 0 12px;
  font-size: rem(16);
  font-weight: bold;
  color: $c_white;

  @include mq(md) {
    font-size: rem(20);
  }
}

.footer-copyright {
  color: $c_white;
}
