/* ----------------------------------------
	共通部分
---------------------------------------- */
@use "setting"as *;
@use "../global"as *;

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family:
    "Helvetica Neue",
    Arial,
    "Hiragino Kaku Gothic ProN",
    "Hiragino Sans",
    Meiryo,
    sans-serif;
  line-height: 1.75;
  font-size: rem(14);
  @include mq(md) {
    font-size: rem(16);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.1;
}

a {
  color: $c_link;
  text-decoration: none;
}

a:hover {
  color: $c_hover;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  image-rendering: optimize-contrast; //Chromeで縮小画像がぼやけるのを防ぐ
}
