/* vision-media
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.vision-media {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mq(md) {
    flex-direction: row;
  }
}

.vision-media__img-wrapper {
  margin-right: 0;
  margin-bottom: 20px;

  @include mq(md) {
    flex: 0 1 40%; //画像サイズの幅をレイアウト幅で割る
    margin-right: 3%; //画像間の空白をレイアウト幅で割る
    margin-bottom: 0;
  }
}

.vision-media__img {
  width: 100%;
}

.vision-media__body {
  flex: 1;
}

.vision-media__title {
  margin-bottom: 20px;
  text-align: center;
}

.vision-media__title-emphasis {
  font-size: rem(32);
}

.vision-media__list {
  list-style-position: inside;
}

.vision-media__item {
  margin-left: 1em;
  text-indent: -1em;

  @include mq(md) {
    margin-bottom: 10px;
  }
}
