/* skill-list
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.skill-list {
  list-style: none;
}

.skill-list__img-wrapper {
  position: relative;
  width: 250px;
  padding-top: 66%;
  margin-right: auto;
  margin-bottom: 15px;
  margin-left: auto;
  overflow: hidden;

  @include mq(sm) {
    padding-top: 42%;
  }

  @include mq(md) {
    width: 200px;
    padding-top: 80%;
  }
}

.skill-list__img {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
}

.skill-list__title {
  margin-bottom: 15px;
  text-align: center;
}
