/* ----------------------------------------
	Form Parts
---------------------------------------- */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

/* 各フォーム部品の基本スタイルを上書き */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
textarea,
select {
  width: 100%;
  padding: 8px;
  font-size: rem(16);
  border: 1px solid $c_key1;
  border-radius: 5px;
  appearance: none;
  appearance: none; /* ブラウザ標準スタイルを無効にする */
}

input[type="radio"] {
  margin-right: 5px;
}

.form__item {
  padding-bottom: 30px;
}

.form__ttl {
  display: inline-block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form__btn {
  margin-top: 15px;
  text-align: center;
}

/* 補足説明文
--------------------------------- */
.inputNote {
  margin-left: 5px;
  font-size: rem(12);
  font-weight: normal;
}

/* セレクトボックス
------------------------------------ */
select {
  display: block;
  width: 100%;
  padding: 8px;
  border: 1px solid #aaa;
  border-radius: 5px;
  appearance: none;
  appearance: none;

  @include mq(md) {
    width: 300px;
  }
}

.radio-list {
  display: flex;
  flex-direction: column;

  @include mq(md) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.radio-item:not(:last-of-type) {
  margin-right: 35px;
}

/* 必須・任意ラベル
--------------------------------- */
.label-required {
  margin-left: 10px;
  font-size: rem(12);
  color: $c_key1;
}
