/* heading-lv2
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.heading-lv2 {
  margin-bottom: 20px;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.1em;

  @include mq(md) {
    margin-bottom: 30px;
    letter-spacing: 0.2em;
  }
}

.heading-lv2__main {
  font-size: rem(36);

  @include mq(md) {
    font-size: rem(48);
    letter-spacing: 0.1em;
  }
}

.heading-lv2__sub {
  display: inline-block;
  font-size: rem(12);
  background: linear-gradient(transparent 50%, $c_key4 50%);

  @include mq(md) {
    font-size: rem(16);
  }
}
