// ============================================================
// 設定
// ============================================================
@use "../global/" as *;

// コンテナーの左右余白
$padding-side: 20px;
$padding-side-sp: 4%;

// ページの全体の横幅（大）
//$width_lg : 1080px;

// ページの全体の横幅（標準）
$width: 1084px;

// ページの全体の横幅（中間）
$width_md: 800px;

// ページの全体の横幅（狭い）
//$width_sm : 800px;

// レイアウト幅
$layout-width: $width + $padding-side * 2;

//標準フォントサイズ(sp)
//$fz_pc: 14px;

//標準フォントサイズ(pc)
//$fz_pc: 16px;

//白黒
$c_white: #fff;
$c_black: #000;

//グレー
$c_gray1: #ddd;
$c_gray2: #707070;

// $c_gray2: #BEBEBE;
// $c_gray3: #707070;
// $c_gray4: #E8E8E9;
// $c_gray5: #505050;

//キーカラー
$c_key1: #68cfc3;
$c_key2: #c8efea;
$c_key3: #1f91be;
$c_key4: #fbe481;

//黄色系
// $c_yellow1: #FFD800;

//青系
// $c_blue1: #1286EA;
// $c_blue2: #3D98B4;
// $c_blue3: #BAF4FF;

//緑系
// $c_green1: #02964E;
// $c_green2: #EEF3F1;

//赤系
// $c_red1: #F7320E;
// $c_red2: #F7E6E9;
// $c_red3: #FCF2F2;

// 標準テキストカラー
$c_text: #333;

// リンクカラー
$c_link: $c_key1;
$c_hover: $c_key3;
$c_active: $c_key2;

// z-index
$layer: (
  hamburger: 100,
  header: 20,
  page-top: 10,
  badge: 2,
  default: 1,
  back: -1
);

@mixin z-index($key) {
  z-index: map-get($layer, $key);
}
