/* swiper
------------------------------------------------------ */
@use "../global/" as *;
@use "../base/setting" as *;

.swiper-container {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.swiper {
  position: relative;
  width: 100%;
  height: 100%;
  padding-bottom: 39px;
} //スライダーコントロールを下部に表示させるための余白を設置する場合
.swiper-wrapper {
  width: 100%;
  height: 100%;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  font-family: "Font Awesome 5 Free";
  font-size: rem(24);
  font-weight: 900;
  color: $c_key1;
}

.swiper-button-prev::after {
  content: "\f060";
}

.swiper-button-next::after {
  content: "\f061";
}

//疑似要素の矢印をホバーで色を変更
.swiper-button-prev:hover::after,
.swiper-button-next:hover::after {
  color: $c_key3;
}

.swiper-pagination-bullet-active {
  background: $c_key1;
}
