/* page-top
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global/"as *;

.page-top {
  @include z-index(page-top);

  position: fixed;
  right: 20px;
  bottom: 20px;
  display: none;

  @include mq(md) {
    right: 45px;
    bottom: 45px;
  }
}

.page-top__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  color: $c_key1;
  background-color: $c_white;
  border-radius: 50%;
  box-shadow: 0 0 6px rgb(0 0 0 / 20%);
}

.page-top__link:hover {
  color: $c_white;
  background-color: $c_key3;
}

.page-top__link::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 600;
  content: "\f062";
}
