/* mv
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.mv {
  height: 100vh;
  background-image: url(../../images/mv.png);
  background-position: center;
  background-size: cover;

  @include flex-center; }

.mv__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  aspect-ratio: 16/9;
  padding: 20px;
  text-align: center;
  background-color: $c_white;

  @include mq(md) {
    max-width: 500px;
    padding: 45px;
  }
}

.mv__logo-main {
  max-width: 200px;
  margin-bottom: 10px;

  @include mq(md) {
    max-width: 350px;
    margin-bottom: 20px;
  }
}

.mv__logo-sub {
  max-width: 100px;

  @include mq(md) {
    max-width: 200px;
  }
}
