/* works-media
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.works-media-list {
  border-top: 1px dashed $c_key1;
}

.works-media {
  display: flex;
  flex-direction: column;
  padding: 40px 0;
  border-bottom: 1px dashed $c_key1;

  @include mq(md) {
    flex-direction: row;
  }
}

.works-media__head {
  margin: 0 auto 20px;

  @include mq(md) {
    flex: 0 1 40%; //画像サイズの幅をレイアウト幅で割る
    margin-right: 3%; //画像間の空白をレイアウト幅で割る
    margin-bottom: 0;
  }
}

.works-media__img-wrapper {
  margin-bottom: 50px;
}

.works-media__img {
  width: 100%;
}

.works-media__btn-area {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.works-media__btn {
  width: 160px;
}

.works-media__btn--center {
  margin: 0 auto;
}

.works-media__btn:not(:last-of-type) {
  margin-right: 15px;
}

.works-media__body {
  flex: 1;
}

.works-media__info {
  text-align: center;
}

.works-media__info-title {
  font-weight: bold;
}

.works-media__title {
  padding-bottom: 5px;
  border-bottom: 1px solid $c_text;
}

.works-media__text:not(:last-of-type) {
  margin-bottom: 10px;
}
