/* thanks
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global" as *;

.thanks {
  @include flex-center;

  height: 100vh;
}

.thanks__inner {
  padding: 30px;
  text-align: center;
  background-image: url(../../images/bg_thanks_01.svg), url(../../images/bg_thanks_02.svg);
  background-position: left -4px top -10px, right -75px top -10px;
  background-size: 200px;

  @include mq(md) {
    padding: 70px;
    background-position: left -26px top -26px, right -142px top -26px;
    background-size: 330px;
  }
}

.thanks__br {
  display: inline-block;

  @include mq(md) {
    display: inline;
  }
}

.thanks__head {
  margin-bottom: 20px;
  font-size: rem(20);
  line-height: 1.5;
  letter-spacing: 0.1em;

  @include mq(md) {
    font-size: rem(32);
    letter-spacing: 0.3em;
  }
}

.thanks__marker {
  background: linear-gradient(transparent 50%, $c_key4 50%);
}

.thanks__body {
  margin-bottom: 20px;
  font-size: rem(14);
}
