/* header
------------------------------------------------------ */
@use "../base/setting"as *;
@use "../global"as *;

.header {
  position: fixed;
  width: 100%;
  height: auto;
  padding: 20px 0;
  transition: all 0.3s;

  @include z-index(header);

  @include mq(md) {
  }
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: $width;
  padding: 0 $padding-side-sp;

  @include mq(md) {
    padding: 0 $padding-side;
    margin: 0 auto;
  }
}

.header--active {
  background-color: $c_white;
  box-shadow: 0 1px 6px 0 rgb(32 33 36 / 28%);
}

.header-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  background-color: $c_key2;
  opacity: 0;
  transition: opacity 0.5s;

  @include z-index(header);

  @include mq(md) {
    position: static;
    width: auto;
    height: auto;
    visibility: visible;
    background-color: transparent;
    opacity: 1;
  }
}

.header-nav.fade {
  visibility: visible;
  opacity: 1;
}

.header-nav__list {
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  list-style: none;
  transform: translate(-50%, -50%);

  @include z-index(header);

  @include mq(md) {
    position: static;
    display: flex;
    transform: none;
  }
}

.header-nav__link {
  display: block;
  margin-bottom: 20px;
  font-size: rem(20);
  font-weight: bold;
  color: $c_text;

  @include mq(md) {
    margin-right: 12px;
    margin-bottom: 0;
    margin-left: 12px;
    font-size: rem(20);
  }
}
